import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Progress, Alert } from 'reactstrap';
import Pusher from 'pusher-js';
import { getSeats, loadSeats, getRequests } from '../../../redux/seatsRedux';
import './SeatChooser.scss';

const SeatChooser = ({ chosenDay, chosenSeat, updateSeat }) => {
  const dispatch = useDispatch();
  const seats = useSelector(getSeats);
  const requests = useSelector(getRequests);
  const totalSeats = 50;

  useEffect(() => {
    const pusherKey = process.env.REACT_APP_PUSHER_KEY;
    const pusherCluster = process.env.REACT_APP_PUSHER_CLUSTER;
    console.log('Initializing Pusher with key:', pusherKey, 'and cluster:', pusherCluster);
    
    const pusher = new Pusher(pusherKey, { cluster: pusherCluster });
    
    pusher.connection.bind('connected', () => {
      console.log('Pusher connection established');
    });
    
    pusher.connection.bind('error', (err) => {
      console.error('Pusher connection error:', err);
    });
    
    const channel = pusher.subscribe('seats-channel');
    
    channel.bind('pusher:subscription_succeeded', () => {
      console.log('Subscribed to seats-channel successfully');
    });
    
    channel.bind('seats-updated', (updatedSeats) => {
      console.log('Received seats-updated event from Pusher:', updatedSeats);
      dispatch(loadSeats(updatedSeats));
    });
    
    return () => {
      channel.unbind_all();
      channel.unsubscribe();
      pusher.disconnect();
    };
  }, [dispatch]);
  

  const isTaken = (seatId) =>
    seats.some(item => item.seat === seatId && item.day === chosenDay);

  const prepareSeat = (seatId) => {
    if (seatId === chosenSeat)
      return <Button key={seatId} className="seats__seat" color="primary">{seatId}</Button>;
    if (isTaken(seatId))
      return <Button key={seatId} className="seats__seat" disabled color="secondary">{seatId}</Button>;
    return (
      <Button
        key={seatId}
        color="primary"
        className="seats__seat"
        outline
        onClick={(e) => updateSeat(e, seatId)}
      >
        {seatId}
      </Button>
    );
  };

  const freeSeatsCount = totalSeats - seats.filter(item => item.day === chosenDay).length;

  return (
    <div>
      <h3>Pick a seat</h3>
      <div className="mb-4">
        <small id="pickHelp" className="form-text text-muted ms-2">
          <Button color="secondary" /> – seat is already taken
        </small>
        <small id="pickHelpTwo" className="form-text text-muted ms-2">
          <Button outline color="primary" /> – it's empty
        </small>
      </div>
      <div className="seats">
        {[...Array(totalSeats)].map((_, i) => prepareSeat(i + 1))}
      </div>
      <p>Free seats: {freeSeatsCount}/{totalSeats}</p>
      {requests['LOAD_SEATS'] && requests['LOAD_SEATS'].pending && (
        <Progress animated color="primary" value={50} />
      )}
      {requests['LOAD_SEATS'] && requests['LOAD_SEATS'].error && (
        <Alert color="warning">Couldn't load seats...</Alert>
      )}
    </div>
  );
};

export default SeatChooser;
